
















import { Component, Vue, Prop } from 'vue-property-decorator'
import * as api from '@/api'
import Section from './Section.vue'

@Component({
  components: {
    CSection: Section
  }
})
export default class Content extends Vue {
  @Prop() data
  isGroupContent (data) {
    return data.every(n => 'section' in n)
  }
}
