



























































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import * as api from '@/api'
import Prismic from 'prismic-javascript'
import Article from '@/components/Article.vue'
import VueGoodshareFacebook from 'vue-goodshare/src/providers/Facebook.vue'
import VueGoodshareTumblr from 'vue-goodshare/src/providers/Tumblr.vue'
import VueGoodshareTwitter from 'vue-goodshare/src/providers/Twitter.vue'
// import VueGoodshareWeibo from "vue-goodshare/src/providers/Weibo.vue"

@Component({
  components: {
    CArticle: Article,
    VueGoodshareFacebook,
    VueGoodshareTumblr,
    VueGoodshareTwitter
    // VueGoodshareWeibo
  }
})
export default class Project extends Vue {
  article = null as any
  next = null as any
  prev = null as any
  navReady = false
  showSharePanel = false

  @Watch('$route')
  reload () {
    this.prev = null
    this.next = null
    this.article = null
    this.navReady = false
    this.load()
  }

  created () {
    this.load()
  }

  async load () {
    const lang = this.$route.params.lang
    this.article = await api.getByUID('project', this.$route.params.uid, { lang })

    if (!(this.article && this.article.id)) return
    const { id } = this.article

    await api.getByQuery(
      Prismic.Predicates.at('document.type', 'project'),
      {
        orderings: '[document.last_publication_date]',
        pageSize: 1,
        after: id,
        lang
      })
      .then(res => {
        if (res.results.length) {
          this.next = res.results[0]
        }
      })

    await api.getByQuery(
      Prismic.Predicates.at('document.type', 'project'),
      {
        orderings: '[document.last_publication_date desc]',
        pageSize: 1,
        after: id,
        lang
      })
      .then(res => {
        if (res.results.length) {
          this.prev = res.results[0]
        }
      })
    this.navReady = true
  }

  mounted () {
  }
}
